<template>
    <div>
        <infinite-scroll :data="data" ref="infiniteScroll" @pullup="pullup" >
            <mt-cell v-for="item in data" :key="item.id" title="测试" :value="item.value" ></mt-cell>
        </infinite-scroll>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

export default {
    data() {
        return {
            data: [],
            loadingMore: false
        };
    },
    created() {
        let _data = [];
        for(let i = 0; i < 10; i++) {
            _data.push({ id: i, value: +new Date() });
        }
        this.data = _data;
    },
    computed: {
    },
    components: {
    },
    methods: {
        pullup (){
            console.log('pullup');
            if( this.loadingMore ) {
                return;
            }
            console.log('loadingMore');
            this.loadingMore = true;
            setTimeout( () => {
                let _data = [];
                for(let i = 0; i < 10; i++) {
                    _data.push({ id: i, value: '新数据：' + +new Date() });
                }
                this.data = this.data.concat( _data );
                this.loadingMore = false;
            }, 2000);
        }
    }
};
</script>


<style lang="scss">
    
    

</style>



