<template>
    <div>
        <mt-cell title="页面" to="test-page-1"  value="this test page 1"></mt-cell>
        <mt-cell title="store" v-model="testIndexStoreText" ></mt-cell>
        <mt-cell title="操作" @click.native="updateStore" is-link value="点我可以更新 store"></mt-cell>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

export default {
    data() {
        return {
        };
    },
    created() {
    },
    computed: {
        ...mapGetters(['testIndexStoreText'])
    },
    components: {
    },
    methods: {
        updateStore() {
            this.$store.dispatch('updateTestIndexStoreText', "更新了..." + +new Date());
        }
    }
};
</script>


<style lang="scss">
    
    

